.help_text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #666666;
}

.save_draft_btn {
  width: 90%;
  height: 56px;
  border: 1px solid #1b6cfc;
  box-sizing: border-box;
  border-radius: 65px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  margin-top: 10px;
  background: #fff;
  color: #1b6cfc;
}

.post_internship_btn {
  width: 90%;
  height: 56px;
  border: 1px solid #1b6cfc;
  box-sizing: border-box;
  border-radius: 65px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  color: #fff;
  background: #1b6cfc;
  margin-top: 10px;
}

.internship_details_header{
    width:100%;
    height: 48px; 
padding: 8px 16px;
/* Primary color */
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 20px; 
border-radius: 10px 10px 0 0;
color: #FFFFFF;
background: #1B6CFC;
}
.internship_details_card{
    width: 90%; 
    background: #FFFFFF; 

box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
border-radius: 10px;
}
.collaps_data{
    width: 80%;
}

.collapse_check_item{
    padding: 8px 16px;
}