.normal_title{
    font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 20px; 
color: #666666;
}
.anaylytics {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
}
.anaylytics_section {
    background: #fff;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 15px;
    padding: 10px;
    width: 183px;
    height: 310px;
}

.anaylytics_section_2 {
    background: #fff;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 15px;
    width: 183px;
    height: 310px;
    padding: 10px;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}

.col_chart {
    background: #1B6CFC;
    border-radius: 10px;
    width: 130px;
    height:15px;
}

.anaylytics_content  h3{
    font-size: 14px;
}

.title {
    color: #00BB00;
}
